@import (less) "./../less/color.less";
@fontSizeTitle: 0.875em;
.floaterTooltip {
    min-height: 100%;
    background-color: #333333;
    color: #fff;
    font-size: 0.813em;
    font-family:  Arial;
    padding: 1em;
    cursor: pointer;
    width: 12.14em;
}

.floater-container:hover {
    background-color: #5AA942 !important;
}

.floater-tooltip {
    background-color: #5AA942 !important;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 0.875em;
}

.iconMargin{
    // margine inserito per centrare le icon intesa
    // tali icone sono allineate al top.
    margin-top:7.5px;
    margin-left:-1px !important;
}

.iconMarginAssistenza{
    // margine inserito per centrare le icon intesa
    // tali icone sono allineate al top.
    margin-top:3.5px;
    margin-left:-1px !important;

}

.floaterTooltipEmpty {
    height: 0%;
    width: 0%;
    padding: 0;
}

.boxPrincipale {
    margin-top:-1px;
    font-weight: 400;
    font-family: Arial;
    font-size: 13px;
    background-color: #258900;
    padding: 0;
    width: 50px;
    height: 50px;
    text-align: center;
    cursor: pointer;
    border-bottom:0px solid #258900
}

