}

.posizioneFissaFloater {
    position: fixed;
    top: 50%;
    transform: translatey(-50%);
}

:host-context(.contrasto-on) {

    .floater-container:hover {
        background-color: @white !important;
    }

    .floater-tooltip {
        background-color: @white !important;
        color: @black !important;
        border: 1px solid @black;
        border-left: none;
    }

    .floaterTooltip {
        background-color: @black;
        color: @white;
    }

    .boxPrincipale {
        background-color: @white;
    }

    .boxPrimarioGreen {
        background-color: @white;
        border-right: 1px solid @black;
        border-top: 1px solid @black;
        .iconHelp {
            color: @black;
        }
    }

    .box-operazioni {
        .boxPrincipale {
            border-right: 1px solid @black;
            &.floater-container:hover {
                border-right: none;
            }
        }
    }

    .boxSecondarioBlack {
        background-color: @black;
    }

    .boxSecondarioGray {
        background-color: @black;
    }

    .boxSecondarioBlack1 {
        background-color: @black;
    }

    .boxSecondarioGray1 {
        background-color: @black;
    }

    .boxFrecciaOperazioni {
        background-color: @white;
        border-bottom: 1px solid @black;
        .iconFreccia {
            color: @black;
            border-right: 1px solid @black;
            border-top: 1px solid @black;
        }
    }

    .boxFrecciaAssistenzaBlack {
        background-color: @black;
    }

    .boxFrecciaAssistenzaGray {
        background-color: @black;
    }

    .iconParlaConNoi {
        color: @black;
    }

    .iconAssistenza {
        color: white;
    }

    .iconHelp {
        color: white;
    }

    .textIcon {
        color: white;
    }
}
